@import '../vars';
@import '../Elements.scss';

.headerform{
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.headerform__container{
    border-radius: 20px;
    background-color: #fff;
    position: relative;
    width: 100%;
}
.headerform__form{
    font-size: .8rem;
    color: #000;
    width: 100%;
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 10px;
    
    &>div{
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 5px;
        label{
            font-weight: bold;
        }
        input{
            width: 100%;
            border: .5px solid rgba($colorBlack, .3);
            padding: 5px 10px;
            font-size: 1.2rem;
        }
        input[type=submit]{
            width: 50%;
            margin: 0 auto;
        }
    }
}

@media screen and (max-width:600px) {
    .headerform__container{
        position: relative;
        width: 70%;
    }
    .headerform__form{
        padding: 10px;
        gap: 20px;
        &>div{
            // flex-direction: column;
        }
    }
}
@media screen and (max-width:400px) {
    
    .headerform__container{
        position: relative;
        width: 90%;
    }
    .headerform__form{
        padding: 10px;
        gap: 20px;
        &>div{
            flex-direction: column;
        }
    }
}