@import '../vars';

.twocolumnsgrid{
    width: 100%;
}
.cta__container{
    background-color: $colorExtra;
    color: $colorWhite;
    padding: 100px 0;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}
.cta__column-one{
    width: 100%;
    display: flex;
    justify-content: left;
    align-items: center;
    flex-direction: column;
    padding: 0 10%;
    margin-bottom: 40px;
}
.cta__column-two{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    input{
        width: 50%;
        border: none;
        outline: none;
        font-size: 1.3rem;
        padding: 7px;
    }
    button{
        font-size: 1.3rem;
        padding: 7px;
        outline: none;
        border: none;
        color: $colorWhite;
        background-color: $colorPrimary;
    }
}

@media screen and (max-width:650px) {
    .cta__container{
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
}