@import '../vars';

.pricing{
    padding-top: 70px;
    position: relative;
    width: 100%;
    padding-bottom: 3rem;
}
.pricing__text{
    width: 100%;
    text-align: center;
    width: 50%;
    margin: 0 auto;
    h2{
        margin-bottom: 30px;
    }
}
.pricing__container{
    padding: 0 10%;
    position: relative;
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.pricing__card{
    background-color: $colorWhite;
    position: relative;
    width: 30%;
    padding-bottom: 20px;
    border: 1px solid rgba($colorSecondary, .7);
}

.pricing__cardcontent{
    text-align: center;
    padding: 20px;
    position: relative;
    width: 100%;
    h3{
        font-size: 1.4rem;
        margin-bottom: 20px;
    }
    div{
        margin-bottom: 20px;
        p{
            font-size: .8rem;
            line-height: 1.1rem;
            img{
                width: 25px;
                margin-right: 10px;
            }
        }
    }
}

.pricing__card_price{
    margin-top: 10px;
    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: center;

    p:nth-of-type(1){
        font-size: 3rem;
    }
    p:nth-of-type(2){
        text-align: left;
    }
}

.pricing__card_list{
    display: flex;
    flex-direction: column;
    gap: 7px;
}
.pricing__card_item{
    display: flex;
    align-items: center;
    justify-content: center;
}




@media screen and (max-width: 800px) {
    .pricing__container{
        padding: 0 5%;
    }
}

@media screen and (max-width: 600px) {
    .pricing__container{
        flex-direction: column;
        gap: 40px;
    }
    .pricing__text{
        width: 80%;
        padding-bottom: 5rem;
    }
    .pricing__card{
        width: 60%;
    }
}
@media screen and (max-width: 430px) {
    .pricing__card{
        width: 90%;
    }
}