@import './vars';

.buttonOne{
    background-color: $colorPrimary;
    padding: 5px 20px;
    color: $colorWhite;
    border: none;
    border-radius: 10px;
    outline: none;
    font-weight: 400;
}
.buttonTwo{
    background-color: $colorPrimary;
    padding: 15px 27px;
    color: $colorWhite;
    outline: none;
    border: none;
    border-radius: 30px;
    font-weight: 400;
}

.buttonOne:hover, .buttonTwo:hover{
    outline: 1px solid #fff;
}
