@import '../vars';

.faqs{
    padding-top: 70px;
    position: relative;
    width: 100%;
}
.faqs__text{
    width: 100%;
    text-align: center;
    width: 50%;
    margin: 0 auto;
    padding-bottom: 3rem;
    h2{
        margin-bottom: 30px;
    }
}

.faqs__container{
    padding: 0 10%;
    position: relative;
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}
.faqs__card{
    position: relative;
    width: 30%;
    background: $colorWhite;
    padding: 15px 30px;
    margin-bottom: 2rem;
}

.faqs__cardcontent{
    padding: 0px 20px;
    position: relative;
    width: 100%;
    p{
        font-size: .8rem;
        line-height: 1.1rem;
        margin-bottom: 5px;
    }
    p:first-of-type{
        font-weight: bold;
    }
}




@media screen and (max-width: 800px) {
    .faqs__container{
        padding: 0 5%;
    }
}

@media screen and (max-width: 600px) {
    .faqs__container{
        flex-direction: column;
    }
    .faqs__text{
        width: 80%;
        padding-bottom: 5rem;
    }
    .faqs__card{
        width: 60%;
    }
}
@media screen and (max-width: 430px) {
    .faqs__card{
        width: 90%;
    }
}