@import '../vars';

.testimonials{
    position: relative;
    width: 100%;
}
.testimonials__text{
    width: 100%;
    padding-top: 60px;
    text-align: center;
    width: 50%;
    margin: 0 auto;
    h2{
        margin-bottom: 30px;
    }
}
.testimonials__container{
    padding: 0 10%;
    position: relative;
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.testimonials__card{
    border: 1px solid #ddd;
    position: relative;
    width: 30%;
    background: $colorWhite;
    padding-bottom: 20px;
}
.testimonials__cardcontent{
    padding: 30px;
    position: relative;
    width: 100%;
    img{
        width: 50%;
        margin-bottom: 20px;
    }
    p{
        font-size: .8rem;
        line-height: 1.1rem;
        margin-bottom: 20px;
    }
    div{
        position: relative;
        border-radius: 50px;
        border: 1px solid $colorPrimary;
        width: 50px;
        height: 50px;
        overflow: hidden;

        img{
            position: absolute;
            width: 100%;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }
}




@media screen and (max-width: 800px) {
    .testimonials__container{
        padding: 0 5%;
    }
}

@media screen and (max-width: 600px) {
    .testimonials__container{
        flex-direction: column;
        gap: 40px;
    }
    .testimonials__text{
        width: 80%;
        padding-bottom: 5rem;
    }
    .testimonials__card{
        width: 60%;
    }
}
@media screen and (max-width: 430px) {
    .testimonials__card{
        width: 90%;
    }
}