@import '../vars';

.services{
    padding-top: 70px;
    position: relative;
    width: 100%;
}
.services__text{
    width: 100%;
    padding: 30px 0;
    text-align: center;
    width: 50%;
    margin: 0 auto;
    h2{
        margin-bottom: 30px;
    }
}
.services__container{
    padding: 0 10%;
    position: relative;
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.services__card{
    position: relative;
    width: 30%;
    padding-bottom: 20px;
    border-radius: 20px;
    border: 1px solid rgba($colorSecondary, .6);
    overflow: hidden;
}

.services__cardmedia{
    background-color: $colorSecondary;
    position: relative;
    width: 100%;
    padding: 20px;
    overflow: hidden;
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 7px;
    div{
        position: relative;
        padding: 20px;
        overflow: hidden;
        border-radius: 50%; 
        background-color: $colorWhite;   
        img{
            position: absolute;
            width: 20px;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }
    p{
        color: $colorWhite;
    }
}

.services__cardcontent{
    padding: 20px;
    position: relative;
    width: 100%;
    p{
        font-size: .8rem;
        line-height: 1rem;
        margin-bottom: 10px;
    }
}




@media screen and (max-width: 800px) {
    .services__container{
        padding: 0 5%;
    }
}

@media screen and (max-width: 600px) {
    .services__container{
        flex-direction: column;
        gap: 40px;
    }
    .services__text{
        padding-bottom: 5rem ;
        width: 80%;
    }
    .services__card{
        width: 60%;
    }
}
@media screen and (max-width: 430px) {
    .services__card{
        width: 90%;
    }
}