@import '../vars';
@import '../Elements.scss';

.header{
    background-size: cover;
    background-repeat: no-repeat;
    background-position: left top;
    position: relative;
    width: 100%;
    min-height: 100vh;
}
.header__container{
    background: rgba(#000, .5);
    padding: 40px;
    padding-top: 150px;
    position: relative;
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.header__text{
    color: #fff;
    font-size: 1.5rem;
    width: 100%;

    h1{
        margin-bottom: 20px;
        font-size: 2.2rem;
    }
    p{
        font-size: 1.4rem;
        margin-bottom: 20px;
        line-height: 1.3rem;
    }
}
.header__form{
    position: relative;
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    &>div{
        width: 70%;
        position: relative;
    }
}

@media screen and (max-width:700px){
    .header__container{
        width: 100%;
        margin: 0 auto;
        padding: 0;
        padding-top: 100px;
        gap: 70px;
    }   
    .header__text{
        font-size: 1.4rem;
        text-align: center;
    }
    
    .header__ilustration{
        height: 100%;
        width: 100%;
    }

    .header__form{
        position: relative;
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    
        &>div{
            width: 90%;
            position: relative;
        }
    }

}

@media screen and (max-width:600px){
    .header__container{
        padding-top: 100px;
        gap: 0;
        flex-direction: column;
    }   
    .header__text{
        margin-top: 30px;
        font-size: 1.2rem;
        width: 80%;
    }
}