.twocolumnsgrid{
    width: 100%;
}
.footer__container{
    padding: 30px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap: 20px;
}
.footer__column-one{
    width: 100%;
}
.footer__column-two{
    width: 100%;
}
.footer__column-three{
    width: 100%;
}

.footer__column-one,.footer__column-two,.footer__column-three{
    h4{
        margin-bottom: 20px;
    }
    p{
        margin-bottom: 10px;
        font-size: .8rem;
    }
}


@media screen and (max-width:600px) {
    .footer__container{
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
}