//uncoment retive position on .navbar__toolbar_item to change hover effect
@import '../vars';

.navbar{
    z-index: 1000;
    position:absolute;
    width: 100%;
}
.navbar__container{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 50px;
}
.navbar__toolbar{
    font-size: .8rem;
    padding: 20px 10px 20px 10px;
    color: #fff;
    min-width: 40%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.navbar__toolbar-secondary{
    padding: 20px 10px 20px 10px;
    color: #fff;
    width: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
}

.navbar__logo{
    z-index: 100;
    position: relative;
    width: 100px;
    img{
        position: absolute;
        width: 100%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}


        //mobile
.navbar__mobile{
    position: relative;
    display: none;
    justify-content: space-between;
    align-items: center;
    padding: 30px 13px;
}
.navbar__mobile_icon{
    position: relative;
}
.navbar__mobile_toolbar{
    position: absolute;
    top: 80%;
    width: 95%;
    padding: 20px;
    background-color: $colorPrimary;
    border-radius: 20px;
    text-align: center;
}
.navbar__mobile_item{
    margin-bottom: 1rem;
    color: $colorWhite;
}


@media screen and (max-width: 700px) {
    .navbar__container{
        display: none;
    }
    .navbar__mobile{
        display: flex;
    }
}